"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import mqtt from "mqtt";
import { useSession } from "next-auth/react";

export default function OrdersPage() {
  const { data: session, status } = useSession();
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    if (status === "loading") return; // Á≠âÂæÖ session ËºâÂÖ•

    if (!session) {
      alert("Ë´ãÂÖàÁôªÂÖ•");
      return;
    }

    let client;

    const getOrders = async () => {
      try {
        const response = await fetch("/api/order/list", {
          credentials: "include",
        });
        if (!response.ok) throw new Error("‰º∫ÊúçÂô®ÈåØË™§");

        const data = await response.json();

        // ‚úÖ Âä†‰∏äÈÄôÊÆµÔºöÂè™È°ØÁ§∫ÈÄ≤Ë°å‰∏≠ÁöÑË®ÇÂñÆ
        const ongoingOrders = data.filter(
          (o) => o.status !== "COMPLETED" && o.status !== "CANCELLED"
        );

        setOrders(ongoingOrders);

        // MQTT Ë®ÇÈñ±...
        client = mqtt.connect("wss://broker.emqx.io:8084/mqtt");
        client.on("connect", () => {
          console.log("üì° MQTT Â∑≤ÈÄ£Á∑öÔºàÈ°ßÂÆ¢Ôºâ");
          client.subscribe(`U1113007/orders/updated/+`);
        });

        client.on("message", (topic, message) => {
          try {
            const payload = JSON.parse(message.toString());
            console.log("üì¨ Êî∂Âà∞Ë®ÇÂñÆÊõ¥Êñ∞Ôºö", payload);
            if (payload.orderId) {
              getOrders(); // ÈáçÊñ∞ÊäìË≥áÊñô
            }
          } catch (err) {
            console.error("‚ùå MQTT Ë®äÊÅØËß£ÊûêÈåØË™§", err);
          }
        });
      } catch (err) {
        console.error("‚ùå ËÆÄÂèñË®ÇÂñÆÂ§±ÊïóÔºö", err);
        setOrders([]);
      }
    };

    getOrders();

    return () => {
      if (client) client.end();
    };
  }, [session, status]);

  // ‰ª•‰∏ã‰∏çËÆäÔºåÁúÅÁï•... 
  // getStatusColor, getDisplayStatus, return JSX

  const getStatusColor = (status) => {
    switch (status) {
      case "PENDING":
        return "bg-yellow-100 text-yellow-800";
      case "PREPARING":
        return "bg-blue-100 text-blue-800";
      case "READY":
        return "bg-green-100 text-green-800";
      case "COMPLETED":
        return "bg-gray-100 text-gray-800";
      case "CANCELLED":
        return "bg-red-100 text-red-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  const getDisplayStatus = (status) => {
    switch (status) {
      case "PENDING":
        return "Á≠âÂæÖÊé•ÂñÆ";         // Â∞öÊú™Êé•ÂñÆ
      case "PREPARING":
        return "È§êÈªûË£Ω‰Ωú‰∏≠";       // Â∫óÂì°Â∑≤Êé•ÂñÆÔºåË£Ω‰Ωú‰∏≠
      case "READY":
        return "È§êÈªûË£Ω‰ΩúÂÆåÊàêÔºåË´ãÂèñÈ§ê"; // Ë£Ω‰ΩúÂÆåÊàê„ÄÅ‰ªòÊ¨æÂÆåÊàêÔºåÂ∑≤ÈÄöÁü•ÂèñÈ§ê
      case "COMPLETED":
        return "ÂèñÈ§êÂÆåÊàê";         // È°ßÂÆ¢Â∑≤ÂÆåÊàêÂèñÈ§ê
      case "CANCELLED":
        return "Â∑≤ÂèñÊ∂à";           // Ë®ÇÂñÆË¢´ÂèñÊ∂à
      default:
        return status;
    }
  };


  if (status === "loading") {
    return <p>ËÆÄÂèñ‰∏≠...</p>;
  }

  if (!session) {
    return <p>Ë´ãÂÖàÁôªÂÖ•ÊâçËÉΩÊü•ÁúãË®ÇÂñÆ</p>;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-100 via-pink-100 to-red-100 px-4 sm:px-6 py-8">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-3xl font-bold mb-6 text-center sm:text-left text-gray-800">ÊàëÁöÑË®ÇÂñÆ</h1>

        {orders.length === 0 ? (
          <p className="text-gray-500 text-center sm:text-left">ÊÇ®ÁõÆÂâçÊ≤íÊúâ‰ªª‰ΩïË®ÇÂñÆ„ÄÇ</p>
        ) : (
          <div className="space-y-6">
            {orders.map((order) => (
              <div key={order.id} className="bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition">
                <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-4">
                  <div>
                    <h3 className="text-lg font-bold text-gray-800">Ë®ÇÂñÆ #{order.id.slice(0, 8)}</h3>
                    <p className="text-sm text-gray-500">{new Date(order.createdAt).toLocaleString()}</p>
                  </div>
                  <span className={`mt-2 sm:mt-0 px-3 py-2 rounded-full text-xs font-medium ${getStatusColor(order.status)}`}>
                    {getDisplayStatus(order.status)}
                  </span>
                </div>

                <div className="mb-3 space-y-1">
                  <p className="text-gray-700">
                    <strong>Á∏ΩÈáëÈ°çÔºö</strong> ${order.totalAmount.toFixed(2)}
                  </p>
                  <p className={order.paymentStatus ? "text-green-600" : "text-red-600"}>
                    <strong>‰ªòÊ¨æÁãÄÊÖãÔºö</strong> {order.paymentStatus ? "Â∑≤‰ªòÊ¨æ" : "Êú™‰ªòÊ¨æ"}
                  </p>
                </div>

                <div className="border-t pt-4">
                  <h4 className="text-sm font-semibold mb-2 text-gray-700">È§êÈªûÂÖßÂÆπÔºö</h4>
                  <ul className="space-y-2">
                    {order.items.map((item) => (
                      <li key={item.id} className="flex justify-between text-sm text-gray-600">
                        <span>
                          {item.menuItem?.name || "Êú™Áü•ÂìÅÈ†Ö"} √ó {item.quantity}
                          {item.specialRequest && (
                            <span className="block text-xs text-gray-400">ÂÇôË®ªÔºö{item.specialRequest}</span>
                          )}
                        </span>
                        <span>
                          ${((item.menuItem?.price ?? 0) * item.quantity).toFixed(2)}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>

                {order.status === "READY" && !order.completedAt && (
                  <div className="mt-4 text-center sm:text-right">
                    <Link
                      href={`/orders/${order.id}/complete`}
                      className="inline-block bg-gradient-to-r from-green-500 to-green-700 text-white px-5 py-2 rounded-md hover:opacity-90 transition"
                    >
                      Á¢∫Ë™çÂèñÈ§ê
                    </Link>
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
